import React from 'react';
import axios from 'axios';
import Movies from "./Movies";
import './MovieStyle.css';

class MovieApp extends React.Component{

    state = {
        isLoading : true,
        movie : []
    }
    
    getMovie = async ()=> {
        const {data : {data}} = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
        console.log(data)
        this.setState(current => ({movie : data.movies , totalCnt : data.movie_count , isLoading : !current}));

       // movie.data.status == 'ok' ? alert("success") : alert("not")
    }

    componentDidMount(){
        this.getMovie();
    }

    render(){
        const {isLoading, movie , totalCnt} = this.state;
    
        return (
            <section className="container">
                {isLoading ? <div className="loader"><span className="loader__text">Loading...</span></div>                 
                : 
                <div className= "movies">
                {movie.map(movie =>{
                 return   <Movies key={movie.id}
                        id ={movie.id}
                        title = {movie.title}
                        year = {movie.year}
                        summary = {movie.summary}
                        genre = {movie.genres}
                        image = {movie.medium_cover_image}
                        />
                })}
                </div>
                }
            </section>
        )
    }

}

export default MovieApp;